
@font-face {
    font-family: 'YekanEn';
    src: url('../fonts/NumEn/woff2/YekanBakh-Regular.woff2');
    font-weight: normal;
    font-style: normal
}

@font-face {
    font-family: 'YekanFa';
    src: url('../fonts/NumFa/woff2/YekanBakhFaNum-Regular.woff2');
    font-weight: normal;
    font-style: normal
}

body {
    font-family: 'YekanEn';
    font-size: 14px;
    margin: 0px;
    padding: 0px;
    direction: rtl;
}
* {
    font-family: 'YekanEn';
    font-size: 14px;
}
.p-dialog .p-dialog-header {
    background: var(--blue-100);
}
.p-dialog .p-dialog-footer {
    background: var(--gray-100);
    padding: .8rem .8rem .8rem 1.4rem;
    text-align: left;
}
.p-dropdown-panel .p-dropdown-items .p-dropdown-item .p-dropdown-check-icon {
    position: relative;
    margin-left: 0.5rem;
    margin-right: -0.5rem;
}
.p-dropdown .p-dropdown-clear-icon{
    left:3rem;
    right:auto;
}
.p-dropdown-panel .p-dropdown-header .p-dropdown-filter{
    margin-left:-1.75rem;
    margin-right:0px;
}
.p-input-icon-left > i{
    left:10px;
}

.p-input-icon-right > i{
    right:10px;
}
.field{
    margin-top: 10px;;
}
.img-input{
    width:72px;
    height:72px;
    border:1px solid #b0b0b0;
    border-radius:10px;
    vertical-align:middle;
    cursor:pointer;
}
.p-dialog-header .p-dialog-header-icons{
	position: absolute;
  	right: 10px;
}
.p-dialog-header .p-dialog-title{
	padding-right: 25px;
    margin-top: 3px;
}
.ps-menuitem-root .css-1tqrhto a{
    height: 30px;
    margin-right: 10px;
    color: #053eaa;
}
.simple-menu{
    border:1px solid #b0b0b0;
    padding:2px;
    cursor: pointer;
}
.simple-menu:hover{
    background-color: #dce4f7;
}
.simple-menu-selected{
    background-color: #4444ee;
    color:#FFF;
    padding:2px;
}

